<template>
  <div class="app">
    <div
      class="app__general"
      @mouseover="hideMenu()"
    >
      <div class="app__left">
        <div class="title">
          <h3>
            Un Centro de Salud Mental<br>
            Emocional y Conductual
          </h3>
        </div>
        <div class="vapp">
          <v-app-bar-nav-icon
            color="#fff"
            class="hidden-md-and-up"
            @click="SET_DRAWER_WEB"
          />
        </div>
      </div>
      <div
        id="app__primary"
        class="app__primary"
      >
        <div class="app_primary_up">
          <v-btn
            outlined
            class="text-capitalize"
            small
            plain
            to="/trabaja-con-nosotros"
          >
            <span style="">Únete a nuestro equipo</span>
          </v-btn>
          <v-menu
            offset-y
            open-on-hover
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                outlined
                class="text-capitalize"
                v-bind="attrs"
                small
                plain
                v-on="on"
              >
                Portal Pacientes <v-icon> mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list
              class="list"
              dense
            >
              <v-list-item
                v-for="(item, index) in portalPacientes"
                :key="index"
                class="list-item"
              >
                <v-list-item-title class="list-title">
                  <a
                    v-if="item.href.includes('http')"
                    :href="item.href"
                    target="_blank"
                  >{{ item.texto }}</a>
                  <router-link
                    v-else
                    :to="item.href"
                  >
                    {{ item.texto }}
                  </router-link>
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <v-btn
            outlined
            class="text-capitalize"
            small
            plain
            href="https://www.centrodeterapia.cl/portal_terapeutas/"
            target="_blank"
          >
            <span style="">Login Profesionales</span>
          </v-btn>

          <v-btn
            outlined
            class="text-capitalize"
            small
            plain
            to="/contactanos"
          >
            <span>Contáctanos</span>
          </v-btn>

          <v-menu
            offset-y
            open-on-hover
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                outlined
                class="text-capitalize"
                v-bind="attrs"
                small
                plain
                v-on="on"
              >
                Sucursales <v-icon> mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list
              class="list"
              dense
            >
              <v-list-item
                v-for="(sucursal, index) in sucursales"
                :key="index"
                class="list-item"
              >
                <v-list-item-title class="list-title">
                  <router-link :to="`/sucursales/${sucursal.ToLink}`">
                    {{ sucursal.Nombre }}
                  </router-link>
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
          <v-menu
            offset-y
            open-on-hover
          >
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                outlined
                class="text-capitalize"
                v-bind="attrs"
                small
                plain
                v-on="on"
              >
                ¿Por qué consultar? <v-icon> mdi-menu-down</v-icon>
              </v-btn>
            </template>
            <v-list
              class="list"
              dense
            >
              <v-list-item
                v-for="(item, index) in porQueConsultar"
                :key="index"
                class="list-item"
              >
                <v-list-item-title class="list-title">
                  <router-link :to="item.href">
                    &nbsp;{{ item.texto }}
                  </router-link>
                </v-list-item-title>
              </v-list-item>
            </v-list>
          </v-menu>
        </div>
        <div
          class="app_primary_down text-uppercase"
          justify-md="end"
        >
          <span
            v-for="(sucursal, index) in sucursales"
            :key="index"
          ><v-icon
             small
             color="#fff"
           >mdi-phone</v-icon>
            {{ sucursal.Nombre }}: {{ sucursal.Telefono }}
          </span>
          <span>
            <v-icon
              small
              color="#fff"
            >mdi-phone</v-icon>
            Terapia Online: {{ tolNumbers[Math.floor(Math.random()*tolNumbers.length)] }}
          </span>
        </div>
      </div>
    </div>
    <slot />
  </div>
</template>

<script>
// Utilities
  import { mapMutations, mapState } from 'vuex'

  export default {
    name: 'CoreAppBarBat',
    data () {
      return {
        tolNumbers: [
          '939416114',
          '946223312',
          '939495460',
          '994489485',
          '994481560',
          '990857932',
        ],
      }
    },
    computed: {
      ...mapState('web', ['sucursales', 'porQueConsultar', 'portalPacientes']),
    },
    created () {
      this.imprimeLinks()
    },
    methods: {
      ...mapMutations('web', ['SET_DRAWER_WEB', 'SET_MENU']),
      onClick (e, item) {
        e.stopPropagation()

        if (item.to || !item.href) return

        this.$vuetify.goTo(item.href.endsWith('!') ? 0 : item.href)
      },
      hideMenu (item) {
        this.SET_MENU({ item, value: false })
      },
      imprimeLinks () {
      // console.log(this.sucursales);
      },
    },
  }
</script>
